import React, { useEffect, useState } from 'react';
import { getEnvironments, getAvailableTimes, createReservation, updateReservation, cancelReservation } from '../services/api';
import EnvironmentList from '../components/EnvironmentList';
import Sidebar from '../components/Sidebar';
import ReservationForm from '../components/ReservationForm';

const Environments: React.FC = () => {
  const [environments, setEnvironments] = useState<any[]>([]);
  const [availableTimes, setAvailableTimes] = useState<any[]>([]);
  const [reservations, setReservations] = useState<any[]>([]);
  const [selectedEnvironment, setSelectedEnvironment] = useState<number | null>(null);
  const [error, setError] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    const fetchEnvironments = async () => {
      try {
        setLoading(true);
        const response = await getEnvironments();
        setEnvironments(response.data);
        setLoading(false);
      } catch (err) {
        setError('Erro ao carregar os ambientes');
        setLoading(false);
      }
    };

    fetchEnvironments();
  }, []);

  const handleSelectEnvironment = async (environmentId: number) => {
    setSelectedEnvironment(environmentId);
    try {
      const response = await getAvailableTimes(environmentId);
      setAvailableTimes(response.data);
    } catch (err) {
      setError('Erro ao carregar os horários disponíveis');
    }
  };

  const handleReservation = async (data: { date: string; time: string }) => {
    if (selectedEnvironment === null) {
      setError('Selecione um ambiente antes de fazer a reserva');
      return;
    }

    try {
      await createReservation({
        environmentId: selectedEnvironment,
        date: data.date,
        time: data.time,
      });
      setError('');
      alert('Reserva realizada com sucesso');
      // Atualizar lista de reservas
      const newReservations = await getReservations();
      setReservations(newReservations.data);
    } catch (err) {
      setError('Erro ao realizar a reserva');
    }
  };

  const handleEditReservation = async (id: number, data: { date: string; time: string }) => {
    try {
      await updateReservation(id, data);
      alert('Reserva editada com sucesso');
      // Atualizar lista de reservas
      const newReservations = await getReservations();
      setReservations(newReservations.data);
    } catch (err) {
      setError('Erro ao editar a reserva');
    }
  };

  const handleCancelReservation = async (id: number) => {
    try {
      await cancelReservation(id);
      alert('Reserva cancelada com sucesso');
      // Atualizar lista de reservas
      const newReservations = await getReservations();
      setReservations(newReservations.data);
    } catch (err) {
      setError('Erro ao cancelar a reserva');
    }
  };

  return (
    <div style={{ display: 'flex' }}>
      <Sidebar />
      <div style={{ marginLeft: '250px', padding: '20px' }}>
        <h1>Ambientes Disponíveis</h1>
        {error && <p style={{ color: 'red' }}>{error}</p>}

        {loading ? (
          <p>Carregando...</p>
        ) : (
          <EnvironmentList environments={environments} onSelect={handleSelectEnvironment} />
        )}

        {selectedEnvironment && (
          <div style={{ marginTop: '20px' }}>
            <h2>Horários Disponíveis</h2>
            {availableTimes.length > 0 ? (
              <ReservationForm availableTimes={availableTimes} onReserve={handleReservation} />
            ) : (
              <p>Selecione um ambiente para ver os horários disponíveis.</p>
            )}

            <h2>Reservas Realizadas</h2>
            {reservations.length > 0 ? (
              <ul>
                {reservations.map((reservation) => (
                  <li key={reservation.id}>
                    {reservation.date} - {reservation.time}
                    <button onClick={() => handleEditReservation(reservation.id, { date: reservation.date, time: reservation.time })}>
                      Editar
                    </button>
                    <button onClick={() => handleCancelReservation(reservation.id)}>Cancelar</button>
                  </li>
                ))}
              </ul>
            ) : (
              <p>Não há reservas feitas para este ambiente.</p>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default Environments;
